import home_page_1 from "@/public/images/home_page_1.svg";
import home_page_2 from "@/public/images/home_page_2.svg";
import home_page_3 from "@/public/images/home_page_3.svg";
import home_page_4 from "@/public/images/home_page_4.svg";
import home_page_5 from "@/public/images/home_page_5.svg";

const Home = () => {
  return (
    <div className="relative overflow-hidden">
      {/* الخلفية */}
      <img src={home_page_1} className="absolute top-0 left-0 w-full h-full object-cover z-0" alt="background" />
      
      {/* الصورة الأولى */}
      <img src={home_page_2} className="absolute top-16 left-10 z-10" alt="foreground" />
      
      {/* الصور الأخرى (يمكنك إضافة المزيد بنفس الأسلوب) */}
      <img src={home_page_3} className="absolute bottom-0 right-0 z-20" alt="additional" />
      <img src={home_page_4} className="absolute bottom-10 left-10 z-20" alt="additional" />
      <img src={home_page_5} className="absolute bottom-20 right-10 z-20" alt="additional" />
      
      {/* المحتوى النصي */}
      <div className="relative z-30 text-center">
        <h1 className="text-white font-bold text-4xl">مع فريق إييا با ميديا</h1>
        <p className="text-white mt-4">إبداع، احترافية، حلول مبتكرة</p>
        <button className="mt-4 py-2 px-6 bg-blue-500 text-white rounded-lg">آراء وتقيمات عملائنا</button>
      </div>
    </div>
  );
};

export default Home;
